.kebabInstructionTitle {
  color: #ce4824;
  font-size: 20px;
  margin-bottom: 5px;
}

div.kebabLine {
  text-align: center;
  background-image: url(./images/line.png);
  width: 60%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.kebabSteps div.step1,
div.kebabSteps div.step2,
div.kebabSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #ce4824;
}

@media (min-width: 600px) {
  .kebabInstructionTitle {
    font-size: 35px;
  }
  div.kebabLine {
    height: 20px;
  }
}
