@media (min-width: 1024px) {
  div.kebabSteps_desktop {
    display: flex;
    justify-content: space-between;
  }

  h2.kebabInstructionTitle_desktop {
    font-size: 26px;
    color: #ce4824;
    margin-bottom: 5px;
    font-weight: 600;
    margin-top: 0;
  }
  div.kebabLine_desktop {
    margin-top: -0.5%;
    background-image: url(../images/line.png);
    height: 15px;
    width: 260px;
    background-repeat: no-repeat;
    background-size: contain;
  }

  p.kebabInstruction_desktop {
    margin-top: 10px;
    font-size: 16px;
    color: #ce4824;
  }

  div.step_desktop3 p.kebabInstruction_desktop {
    min-width: 220px;
  }
}

@media (min-width: 1280px) {
  p.kebabInstruction_desktop {
    font-size: 18px;
  }
}

@media (min-width: 1600px) {
  h2.kebabInstructionTitle_desktop {
    font-size: 40px;
  }
  div.kebabLine_desktop {
    height: 23px;
    width: 400px;
  }

  p.kebabInstruction_desktop {
    font-size: 22px;
  }
}
