div.kebabInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.kebabArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arrow-mobile.png");
}

.kebabNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.kebabTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.kebabLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.kebabLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.kebabNutritionalValue {
  display: flex;
}

div.kebabNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.kebabAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.kebabLineInformations {
    width: 310px;
  }
  div.kebabLineAlergens {
    width: 140px;
  }
  p.kebabTitle {
    font-size: 35px;
  }
  div.kebabNutritionalValue,
  .kebabAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.kebabArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.kebabInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.kebabTitle {
    font-size: 23px;
    color: #ce4824;
    font-weight: 700;
  }
  div.kebabLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineKebab.png);
    background-size: contain;
    background-position: center;
  }

  div.kebabLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineKebab.png);
    background-position: center;
  }
  .kebabNutritionalValues {
    color: #ce4824;
    transition: 0.1s;
  }

  div.kebabNutritionalValue,
  .kebabAlergensDescription {
    font-size: 12px;
  }

  .kebabAlergensDescription {
    color: #ce4824;
    display: block;
  }
}
